.hat {
    display: flex;
    justify-content: space-between;
}
.castaway_hat {
    margin-left: 3vw;
    margin-top: 3vw;
    color: white;
    font-weight: bold;
    font-size: 3vw;
    align-self: center;
}
.menu {
    display: flex;
    justify-content: space-around;
    margin-right: 1.5vw;
    margin-top: 3vw;
    margin-bottom: 3vw;
}
.menulink {
    font-family: "DM Sans", sans-serif;
    color: white;
    margin-right: 2vw;
    font-size: 1.5vw;
    align-self: center;
    text-decoration: none;
    transition: color 0.3s;
}
.menulink:hover {
    color: rgba(17, 141, 168, 1);
    transition: color 0.3s;
}
.poster {
    display: flex;
    margin-left: 4vw;
    margin-right: 4vw;
}
.face {
    height: 48vw;
    border-radius: 25px;
}
.header {
    color: white;
    font-family: "DM Sans", sans-serif;
    font-size: 7vw;
    margin-left: 3vw;
    margin-top: 7vw;
    margin-bottom: 0;
    letter-spacing: 0.4vw;
}
.listenon {
    color: white;
    font-family: "DM Sans", sans-serif;
    margin-left: 4vw;
    font-size: 2vw;
}
.sociallinks {
    display: flex;
    margin-left: 4vw;
}
.social {
    margin-right: 2vw;
    width: 3.5vw;
}
.latestall {
    display: flex;
    justify-content: space-between;
    margin-left: 4vw;
    margin-right: 4vw;
    margin-top: 5vw;
    margin-bottom: 3vw;
}
.latest {
    font-family: "DM Sans", sans-serif;
    font-size: 5.8vw;
    color: white;
    letter-spacing: 0.4vw;
}
.all {
    font-size: 1.5vw;
    color: white;
    background-color: rgba(17, 141, 168, 1);
    border: none;
    border-radius: 10px;
    height: 5vw;
    align-self: center;
    padding-left: 1.5vw;
    padding-right: 1.5vw;
    transition: background-color 0.3s;
    cursor: pointer;
}
.all:hover {
    background-color: rgb(12, 101, 121);
    transition: background-color 0.3s;
}
.reviews {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.about_person {
    display: flex;
    margin-top: 27vw;
    margin-left: 4vw;
    margin-right: 4vw;
    align-items: center;
    margin-bottom: 15vw;
}
.about_text {
    margin-right: 5vw;
}
.arrow {
    width: 8vw;
}
.meet {
    color: rgb(12, 101, 121);
    font-size: 1.5vw;
    margin: 0;
}
.jacob{
    font-family: "DM Sans", sans-serif;
    font-size: 5vw;
    color: white;
    letter-spacing: 0.4vw;
    margin: 0;
}
.about_text_main {
    color: rgba(255, 255, 255, 0.7);
    font-size: 2vw;
    margin: 0;
    margin-top: 1.5vw;
    font-family: "DM Sans", sans-serif;
}
.about_pic {
    height: 48vw;
    border-radius: 25px;
}
.subscribe {
    display: flex;
    background-color: black;
    border-radius: 25px;
    margin-left: 4vw;
    margin-right: 4vw;
    justify-content: space-between;
    margin-bottom: 15vw;
}
.subscribe_text {
    margin-top: 3vw;
    margin-left: 4vw;
    margin-bottom: 5vw;
    margin-right: 4vw;
}
.email_newsletter {
    color: rgb(12, 101, 121);
    font-size: 1.5vw;
    margin: 0;
    font-family: "DM Sans", sans-serif;
}
.subscribe_large {
    font-family: "DM Sans", sans-serif;
    font-size: 5vw;
    color: white;
    letter-spacing: 0.4vw;
    margin: 0;
}
.typeboxes {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 5vw;
    margin-top: 3vw;
    margin-right: 4vw;
}
.subscribe_input {
    background-color: rgb(25, 25, 25);
    color: rgba(255, 255, 255, 0.7) ;
    border: none;
    border-radius: 7px;
    font-size: 3.5vw;
    font-family: "DM Sans", sans-serif;
    padding-left: 1vw;
    padding-top: 1vw;
    padding-bottom: 1vw;
    margin-bottom: 2vw;
}
.submit {
    font-size: 1.5vw;
    color: white;
    background-color: rgba(17, 141, 168, 1);
    border: none;
    border-radius: 10px;
    height: 5vw;
    align-self: flex-start;
    padding-left: 2vw;
    padding-right: 2vw;
    transition: background-color 0.3s;
    cursor: pointer;
}
.submit:hover {
    background-color: rgb(12, 101, 121);
    transition: background-color 0.3s;
}
.bottom_container {
    margin-top: 20vw;
    display: flex;
    justify-content: space-between;
    margin-left: 4vw;
    margin-right: 4vw;
    margin-bottom: 15vw;
}
.bottom_inner_container {
    display: flex;
    flex-direction: column;
}
.castaway_bottom {
    color: white;
    font-weight: bold;
    font-size: 5vw;
    margin-top: 0;
    margin-bottom: 0;
}
.bottom_nav {
    font-family: "DM Sans", sans-serif;
    color: white;
    margin-right: 2vw;
    margin-top: 2vw;
    margin-bottom: 0vw;
    font-size: 1.5vw;
    text-decoration: none;
    transition: color 0.3s;
}
.bottom_nav:hover {
    color: rgba(17, 141, 168, 1);
    transition: color 0.3s;
}
.bottom_social {
    margin-top: 2vw;
}