.article_container {
    display: flex;
    background-color: black;
    border-radius: 3vw;
    margin-left: 4vw;
    margin-right: 4vw;
    margin-bottom: 4vw;
}
.article_pic {
    margin: 3vw;
    width: 25vw;
    align-self: center;
}
.article_text_container {
    margin-top: 3vw;
    margin-bottom: 3vw;
    margin-right: 3vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.article_button {
    background-color: rgb(25, 25, 25);
    color: white;
    border: none;
    width: 12vw;
    border-radius: 5px;
    font-size: 1.5vw;
    margin-bottom: 2.5vh;
    font-family: "DM Sans", sans-serif;
    transition: color 0.3s;
}
.article_button:hover {
    transition: color 0.3s;
    color: rgba(17, 141, 168, 1);
}
.article_number {
    color: rgba(17, 141, 168, 1);
    font-size: 1.5vw;
    font-family: "DM Sans", sans-serif;
    margin: 0;
    padding-bottom: 1.5vh;
}
.article_article {
    font-size: 4vw;
    margin: 0;
    color: white;
    font-family: "DM Sans", sans-serif;
    letter-spacing: 0.3vw;
}
.article_text {
    color: rgba(255, 255, 255, 0.7);
    font-family: "DM Sans", sans-serif;
    margin: 0;
    padding-top: 1.5vh;
    font-size: 1.7vw;
    font-weight: 0;
}
.article_view {
    background-color: rgba(17, 141, 168, 1);
    transition: background-color 0.3s;
    color: white;
    width: 18vw;
    font-family: "DM Sans", sans-serif;
    font-size: 1.7vw;
    border: none;
    border-radius: 10px;
    margin-top: 3vh;
    padding: 1.2vw;
    cursor: pointer;
}
.article_view:hover {
    background-color: rgb(12, 101, 121);
    transition: background-color 0.3s;
}