.review_container {
    background-color: black;
    border-radius: 25px;
    width: 27vw;
    margin-left: 2vw;
    margin-right: 2vw;
    margin-bottom: 2vw;
}
.stars {
    width: 8vw;
    margin-left: 3vw;
    margin-top: 2vw;
}
.review_text {
    color: white;
    font-family: "DM Sans", sans-serif;
    margin-left: 3vw;
    margin-top: 1vw;
    margin-right: 3vw;
    font-size: 2vw;
}
.review_name {
    color: rgba(255, 255, 255, 0.7);
    font-family: "DM Sans", sans-serif;
    margin-left: 3vw;
    margin-top: 1vw;
    margin-right: 3vw;
    font-size: 1.5vw;
}